import React from 'react'
import './Gallery.sass'
import { fadeInDown, slideInLeft, zoomInRight, pulse } from 'react-animations'
import styled, { keyframes } from 'styled-components'
import img0 from '../../img/gallery/gallery__img0.png'
import img1 from '../../img/gallery/gallery__img1.png'
import img2 from '../../img/gallery/gallery__img2.png'
import img3 from '../../img/gallery/gallery__img3.png'
import img4 from '../../img/gallery/gallery__img4.png'
import img5 from '../../img/gallery/gallery__img5.png'
import img6 from '../../img/gallery/gallery__img6.png'
import img7 from '../../img/gallery/gallery__img7.png'
import img8 from '../../img/gallery/gallery__img8.png'

export const Gallery = () => {
  const FadeInDown = styled.div`
    animation: 2s ${keyframes`${fadeInDown}`} 1;
  `
  const SlideInLeft = styled.div`
    animation: 2s ${keyframes`${slideInLeft}`} 1;
  `
  const ZoomInRight = styled.div`
    animation: 2s ${keyframes`${zoomInRight}`} 1;
  `
  const Pulse = styled.div`
    animation: 1.8s ${keyframes`${pulse}`} infinite;
  `
  return (
    <section className="gallery anim anim-no-repeat">
      <div className="gallery__row">
        <SlideInLeft>
          <div className="gallery__unselectedImages">
            <div className="gallery__unselectedImagesRow">
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img1}
                data-material="wood"
              />
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img5}
                data-material="rock"
              />
            </div>
            <div className="gallery__unselectedImagesRow">
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img2}
                data-material="wood"
              />
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img6}
                data-material="rock"
              />
            </div>
            <div className="gallery__unselectedImagesRow">
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img3}
                data-material="wood"
              />
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img7}
                data-material="rock"
              />
            </div>
            <div className="gallery__unselectedImagesRow">
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img4}
                data-material="wood"
              />
              <img
                className="gallery__unselectedImagesItem gallery__img"
                src={img8}
                data-material="rock"
              />
            </div>
          </div>
        </SlideInLeft>
        <div className="gallery__selectedImage">
          <FadeInDown>
            <img
              className="gallery__selectedImageItem gallery__img"
              src={img0}
            />
          </FadeInDown>
          <div className="gallery__selectors">
            <div className="gallery__selectorsRock" data-material="rock">
              Каменные Дома
            </div>
            <div className="gallery__selectorsWood" data-material="wood">
              Деревянные Дома
            </div>
          </div>
        </div>
        <ZoomInRight>
          <div className="gallery__desc">
            <div className="gallery__descRow">
              <div className="gallery__descWorks">
                <h3 className="gallery__descWorksTitle">Состав работ :</h3>
                <p className="gallery__descWorksText">
                  Проектирование, <br />
                  поставка материалы, <br />
                  строительство под ключ <br />
                </p>
              </div>
              <div className="gallery__descWorks">
                <h3 className="gallery__descWorksTitle">Квадратура :</h3>
                <p className="gallery__descWorksText">204 кв/м</p>
              </div>
            </div>
            <div className="gallery__descRow">
              <div className="gallery__descWorks">
                <h3 className="gallery__descWorksTitle">Материалы :</h3>
                <p className="gallery__descWorksText">
                  Материал 1, <br />
                  материал 2, <br />
                  материал 3 <br />
                </p>
              </div>
              <div className="gallery__descWorksDeadline">
                <h3 className="gallery__descWorksTitle">Сроки :</h3>
                <p className="gallery__descWorksText">2 месяца</p>
              </div>
            </div>
            <div className="center">
              <Pulse>
                <button className="gallery__descBtn btn">Скачать проект</button>
              </Pulse>
            </div>
          </div>
        </ZoomInRight>
      </div>
    </section>
  )
}

export default Gallery
