import React from 'react'
import './Steps.sass'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper.scss'
import qa from '../../img/steps/steps__qa.png'
import process from '../../img/steps/steps__process.png'
import economyForecast from '../../img/steps/steps__economy-forecast.png'
import contract from '../../img/steps/steps__contract.png'
import sketch from '../../img/steps/steps__sketch.png'
import construction from '../../img/steps/steps__construction.png'

export const MobileSteps = () => {
  return (
    <section className="mobileSteps steps">
      <h2 className="steps__title">Этапы работы</h2>
      <Swiper
        style={{ height: '150px', maxWidth: '98%', margin: 'auto' }}
        spaceBetween={50}
        slidesPerView={1.5}
        onSlideChange={() => console.log('slide change')}
        onSwiper={(swiper) => console.log(swiper)}
      >
        <SwiperSlide>
          <div className="steps__basicStepsItem">
            <img className="steps__basicStepsItemIcon" src={qa} />
            <h3 className="steps__basicStepsItemTitle">
              Обсуждение <br /> Проекта
            </h3>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="steps__basicStepsItem">
            <img className="steps__basicStepsItemIcon" src={process} />
            <h3 className="steps__basicStepsItemTitle">
              Разработка <br /> Планировки
            </h3>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="steps__basicStepsItem">
            <img className="steps__basicStepsItemIcon" src={economyForecast} />
            <h3 className="steps__basicStepsItemTitle">
              Составление <br /> Сметы
            </h3>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="steps__advancedStepsItem">
            <img className="steps__advancedStepsItemIcon" src={sketch} />
            <h3 className="steps__advancedStepsItemTitle">
              Подготовительные <br /> Строительные <br /> Работы{' '}
            </h3>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="steps__advancedStepsItem">
            <img className="steps__advancedStepsItemIcon" src={construction} />
            <h3 className="steps__advancedStepsItemTitle">Стройка</h3>
          </div>
        </SwiperSlide>
      </Swiper>
      <div className="container" style={{ marginTop: '-50px' }}>
        <section className="offer">
          <h2 className="steps__formTitle">
            Узнайте о типичных ошибках заказчика при строительстве дома
          </h2>
          <h3 className="steps__formSubtitle">
            Подпишитесь на информационную рассылку
          </h3>
          <form className="offer__form">
            <div className="center">
              <input
                className="offer__formName input"
                type="text"
                placeholder="Ваше Имя"
              />
            </div>
            <div className="center">
              <input
                className="offer__formPhone input"
                type="text"
                placeholder="Ваш Email"
              />
            </div>
            <div className="center">
              <button className="offer__formBtn formBtn btn" type="button">
                Закажите расчет проекта уже сегодня!
              </button>
            </div>
          </form>
        </section>
      </div>
    </section>
  )
}

export default MobileSteps
