import React from 'react'
import './Main.sass'
import {
  flash,
  slideInDown,
  pulse,
  slideInLeft,
  fadeInDown,
} from 'react-animations'
import styled, { keyframes } from 'styled-components'
import house from '../../img/main/main__house.png'
import cloud from '../../img/main/main__cloud.png'
import feature1 from '../../img/main/main__feature1.png'
import feature2 from '../../img/main/main__feature2.png'
import feature3 from '../../img/main/main__feature3.png'
import decorRight from '../../img/main/main__decorRight.png'
import decorLeft from '../../img/main/main__decorLeft.png'
export const Main = () => {
  const SlideInLeft = styled.div`
    animation: 2s ${keyframes`${slideInLeft}`} 1;
  `
  const SlideInDown = styled.div`
    animation: 3s ${keyframes`${slideInDown}`} 1;
  `
  const Pulse = styled.div`
    animation: 1.8s ${keyframes`${pulse}`} infinite;
  `
  return (
    <main className="main anim anim-no-repeat">
      <div className="container">
        <h1 className="main__title">
          Дом <span className="main__titleHighlight">с участком</span> по цене{' '}
          <br /> квартиры <span className="main__titleHighlight">в</span>{' '}
          Стерлитамаке!
        </h1>
        <div className="main__body">
          <SlideInDown>
            <img className="main__house" src={house} />
          </SlideInDown>
          <img src={cloud} alt="" className="main__cloud" />

          <Pulse>
            <button className="main__btn formBtn btn">
              Заказать <br /> проект
            </button>
          </Pulse>
        </div>
        <h2 className="main__tagline">
          Строим теплые дома быстро и по доступной цене!
        </h2>
      </div>

      <SlideInLeft>
        <div className="main__features">
          <div className="container">
            <div className="main__featuresItem">
              <img className="main__featuresItemIcon" src={feature1} />
              <h3 className="main__featuresItemDesc">
                Строим <br />{' '}
                <span className="main__featuresItemDescHighlight">
                  "под ключ"
                </span>
              </h3>
            </div>
            <div className="main__featuresItem">
              <img className="main__featuresItemIcon" src={feature2} />
              <h3 className="main__featuresItemDesc">
                Ипотека от <br />{' '}
                <span className="main__featuresItemDescHighlight">
                  ведущик банков
                </span>
              </h3>
            </div>
            <div className="main__featuresItem">
              <img className="main__featuresItemIcon" src={feature3} />
              <h3 className="main__featuresItemDesc">
                Рассрочка <br />{' '}
                <span className="main__featuresItemDescHighlight">
                  без банков
                </span>
              </h3>
            </div>
            <div className="main__decor">
              <img className="main__decorRight" src={decorRight} />
              <img className="main__decorLeft" src={decorLeft} />
            </div>
          </div>
        </div>
      </SlideInLeft>
    </main>
  )
}

export default Main
