import React from 'react'
import './Projects.sass'
import preview from '../../img/projects/projects__preview0.png'

export const MobileProjects = () => {
  return (
    <section className="projects anim anim-no-repeat">
      <div className="container">
        <h2 className="projects__title">Наши проекты</h2>
        <div className="row">
          <div className="projects__sizes">
            <div className="projects__sizesItem">70 кв.м</div>
            <div className="projects__sizesItem">111 кв.м</div>
            <div className="projects__sizesItem">90 кв.м</div>
            <div className="projects__sizesItem">113 кв.м</div>
            <div className="projects__sizesItem">110 кв.м</div>
          </div>
          <div className="projects__sizes">
            <div className="projects__sizesItem">80 кв.м</div>
            <div className="projects__sizesItem">112 кв.м</div>
            <div className="projects__sizesItem">114 кв.м</div>
            <div className="projects__sizesItem">114 кв.м</div>
            <div className="projects__sizesItem">115 кв.м</div>
          </div>
        </div>
        <div className="projects__preview">
          <div className="projects__previewDesc">
            <h4 className="projects__previewDescTitle">Проект</h4>
            <div className="projects__previewDescPlans">
              <p className="projects__previewDescPlansTitle">Стандарт 100</p>
              <p className="projects__previewDescPlansBasic">
                <b>Базовый</b> - 1 535 000 руб.
              </p>
              <p className="projects__previewDescPlansStandart">
                <b>Стандарт</b> - 1 717 520 руб.
              </p>
              <p className="projects__previewDescPlansFull">
                <b>Под ключ</b> - 2 149 980 руб.
              </p>
              <button className="projects__previewDescBtn btn" type="button">
                Заказать звонок
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default MobileProjects
