import React from 'react'
import './Footer.sass'
import logo from '../../img/logo.png'
import map from '../../img/footer/footer__map.png'
export const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer__info">
        <div className="footer__contacts">
          <div className="footer__contactsAddress">
            Стерлитамак, <br /> ул. Профсоюзная 2к1
          </div>
          <div className="footer__contactsPhone">8-965-921-9000</div>
          <h3 className="footer__contactsTagline">Уже работаем!</h3>
        </div>
        <img className="footer__logo" src={logo} alt="Логотип" />
        <img className="footer__map" src={map} alt="Яндекс Карты" />
      </div>
      <div className="footer__id">
        ООО ТЁПЛЫЙ ДОМ ИНН/ОГРН 12345678/12345678
      </div>
    </footer>
  )
}
export default Footer
