import React from 'react'
import './Features.sass'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper.scss'
import keys from '../../img/features/features__keys.png'
import education from '../../img/features/features__education.png'
import guarantee from '../../img/features/features__guarantee.png'
import heart from '../../img/features/features__heart.png'

export const MobileFeatures = () => {
  return (
    <section className="mobileFeatures features">
      <Swiper
        style={{ background: '#3dbf4d' }}
        spaceBetween={50}
        slidesPerView={1.5}
        onSlideChange={() => console.log('slide change')}
        onSwiper={(swiper) => console.log(swiper)}
      >
        <SwiperSlide>
          <div className="features__item">
            <h3 className="features__itemTitle">
              Готовые дома <br /> под ключ
            </h3>
            <a href="#" className="features__itemExpand">
              Подробнее
            </a>
            <img
              className="features__itemIcon features__itemIconKeys"
              src={keys}
            />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="features__item">
            <h3 className="features__itemTitle">Кадры с высшим образованием</h3>
            <a href="#" className="features__itemExpand">
              Подробнее
            </a>
            <img
              className="features__itemIcon features__itemIconKeys"
              src={education}
            />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="features__item">
            <h3 className="features__itemTitle">
              Гарантия <br /> 10 лет
            </h3>
            <a href="#" className="features__itemExpand">
              Подробнее
            </a>
            <img
              className="features__itemIcon features__itemIconKeys"
              src={guarantee}
            />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="features__item">
            <h3 className="features__itemTitle">Строим как себе!</h3>
            <a href="#" className="features__itemExpand">
              Подробнее
            </a>
            <img
              className="features__itemIcon features__itemIconKeys"
              src={heart}
            />
          </div>
        </SwiperSlide>
      </Swiper>
    </section>
  )
}

export default MobileFeatures
