import React from 'react'
import './Features.sass'
import keys from '../../img/features/features__keys.png'
import education from '../../img/features/features__education.png'
import guarantee from '../../img/features/features__guarantee.png'
import heart from '../../img/features/features__heart.png'
import { slideInLeft } from 'react-animations'
import styled, { keyframes } from 'styled-components'

const SlideInLeft = styled.div`
  animation: 1.8s ${keyframes`${slideInLeft}`} infinite;
`

export const Features = () => {
  return (
    <SlideInLeft>
      <section className="features anim anim-no-repeat">
        <div className="container">
          <div className="features__row">
            <div className="features__item">
              <h3 className="features__itemTitle">
                Готовые дома <br /> под ключ
              </h3>
              <a href="#" className="features__itemExpand">
                Подробнее
              </a>
              <img
                className="features__itemIcon features__itemIconKeys"
                src={keys}
              />
            </div>
            <div className="features__item">
              <h3 className="features__itemTitleEdu features__itemTitle">
                Кадры с высшим образованием
              </h3>
              <a href="#" className="features__itemExpand">
                Подробнее
              </a>
              <img
                className="features__itemIcon features__itemIconEdu"
                src={education}
              />
            </div>
            <div className="features__item">
              <h3 className="features__itemTitleGuarant">
                Гарантия <br /> 10 лет
              </h3>
              <a href="#" className="features__itemExpand">
                Подробнее
              </a>
              <img
                className="features__itemIcon features__itemIconGuarant"
                src={guarantee}
              />
            </div>
            <div className="features__item">
              <h3 className="features__itemTitleBuild features__itemTitle">
                Строим как себе!
              </h3>
              <a href="#" className="features__itemExpand">
                Подробнее
              </a>
              <img
                className="features__itemIcon features__itemIconHeart"
                src={heart}
              />
            </div>
          </div>
        </div>
      </section>
    </SlideInLeft>
  )
}

export default Features
