import React from 'react'
import './Header.sass'
import { fadeInLeft, fadeInRight } from 'react-animations'
import styled, { keyframes } from 'styled-components'
import logo from '../../img/header/header__logo.png'
export const Header = () => {
  const FadeInLeft = styled.div`
    animation: 1.2s ${keyframes`${fadeInLeft}`} 1;
  `
  const FadeInRight = styled.div`
    animation: 1.5s ${keyframes`${fadeInRight}`} 1;
  `
  return (
    <header className="header">
      <div className="container" style={{ display: 'flex', flexWrap: 'wrap' }}>
        <FadeInLeft>
          <a className="header__logo" href="./">
            <img className="header__logoImg" src={logo} alt="Логотип" />
          </a>
        </FadeInLeft>
        <FadeInRight>
          <nav className="header__nav">
            <ul className="header__menu">
              <li className="header__menuItem">
                <a className="header__menuLink" href="./">
                  Главная
                </a>
              </li>
              <li className="header__menuItem">
                <a className="header__menuLink" href="./">
                  Наши проекты
                </a>
              </li>
              <li className="header__menuItem">
                <a className="header__menuLink" href="./">
                  Отзывы
                </a>
              </li>
              <li className="header__menuItem">
                <a className="header__menuLink" href="./">
                  Этапы работ
                </a>
              </li>
            </ul>
          </nav>
        </FadeInRight>
        <div className="header__contacts">
          <div className="header__contactsAddress">
            Стерлитамак, <br /> ул. Профсоюзная 2к1
          </div>
          <div className="header__contactsPhone">
            <h4 className="header__contactsPhoneText">8-965-921-9000</h4>
            <h5 className="header__contactsPhoneFreecall">
              Бесплатный звонок!
            </h5>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header
